import React, { useState } from "react";

import { AllCommunityModule, ModuleRegistry } from "ag-grid-community";
import { AgGridReact } from "ag-grid-react";
import './Grid1.css'

ModuleRegistry.registerModules([AllCommunityModule]);

//button to test it
const CustomButtonComponent = (props) => {
  return <button onClick={() => window.alert("clicked")}>Push Me!</button>;
};

const pagination = true;
const paginationPageSize = 100;
const paginationPageSizeSelector = [100, 200, 500, 1000];

// Create new GridExample component
const Grid1 = () => {
  // Row Data: The data to be displayed.
  const [rowData, setRowData] = useState([
    { brand: "Tesla", model: "Model Y", price: 64950, electric: true },
    { brand: "Ford", model: "F-Series", price: 33850, electric: false },
    { brand: "Toyota", model: "Corolla", price: 29600, electric: false },
    { brand: "Mercedes", model: "EQA", price: 48890, electric: true },
    { brand: "Fiat", model: "500", price: 15774, electric: false },
    { brand: "Nissan", model: "Juke", price: 20675, electric: false },
  ]);

  // Column Definitions: Defines & controls grid columns.
  const [colDefs, setColDefs] = useState([
    { headerName: "Brand & Model", valueGetter: p => p.data.brand + ' ' + p.data.model, flex: 2 },
    { field: "brand", filter: true },
    { field: "model", flex: 1 },
    { field: "price", valueFormatter: p => '' + p.value.toLocaleString(), flex: 1 },
    { field: "electric", editable: true,
      cellClassRules: {
          // apply green to electric cars
          'rag-green': params => params.value === true,
      } },
    { field: "button", cellRenderer: CustomButtonComponent, flex: 1 },
  ]);

  const defaultColDef = {
    flex: 1,
  };

  const rowClassRules = {
    // apply red to Ford cars
    'rag-red': params => params.data.brand === 'Toyota',
  };

  // Container: Defines the grid's theme & dimensions.
  return (
    <div className="grid1">
      <AgGridReact
        rowData={rowData}
        columnDefs={colDefs}
        defaultColDef={defaultColDef}
        pagination={pagination}
        paginationPageSize={paginationPageSize}
        paginationPageSizeSelector={paginationPageSizeSelector}
        rowClassRules={rowClassRules}
      />
    </div>
  );
};

export default Grid1;